@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
body{margin:0;font-family:'Space Mono', monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}*,*:before,*:after{box-sizing:border-box}

.detail{display:flex;flex-direction:column;align-items:flex-end;padding-right:48px;text-align:right;color:#333;margin-top:45vh}@media screen and (max-width: 767px){.detail{padding-right:5%}}.detail__heading{font-size:24px;font-weight:400;line-height:1;height:1.3em;background:#333;color:#fff;letter-spacing:-1px;padding-top:.2em;padding-bottom:.1em;white-space:nowrap;margin:0}.detail__text{margin:1em 0 0;font-size:12px;line-height:2;height:4em}.logo{margin-top:16px;display:inline-block;width:40px;height:40px;padding:8px;background:#fff;border-radius:50%;transition:.3s ease;margin-right:-8px}@media screen and (max-width: 767px){.logo{padding:0;width:24px;height:24px;margin-right:0}}@media screen and (min-width: 768px){.logo:hover{box-shadow:0 8px 20px -4px rgba(0,0,0,0.2)}}.logo img{width:24px;height:24px}.skill{position:absolute;left:0;top:20px;font-size:12px}.skill__list{margin:0;padding:0 0 0 20px}.skill__item{height:1em;line-height:1;margin-bottom:1em;padding-left:16px;list-style:none;position:relative}.skill__item:before{position:absolute;left:0;top:0;content:'-'}

